.card {
  min-height: 49rem;
  color: $gray;
  padding: 2.9rem 2.1rem 2.9rem 2.3rem;
  box-shadow: 0 1.4rem 3.9rem -1.1rem rgba(0, 0, 0, 0.06);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;

  &__img-wrapper {
    text-align: center;
    margin-bottom: 0.4rem;
  }
  &__img {
    width: 100%;
    height: 19.4rem;
    object-fit: contain;
    overflow: hidden;
    margin: 0 auto;
  }
  &__content {
    flex: 1 1 auto;
    margin-bottom: 1.5rem;
  }
  &__volume {
    font-size: 1.2rem;
    line-height: 1.6rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 1.1rem;
  }

  &__volume-img {
    margin-right: 0.5rem;
    &--bottle {
      margin-right: 1rem;
    }
  }

  &__name {
    font-weight: 700;
    font-size: 1.6rem;
    line-height: 2.1rem;
    color: $black;
    margin-bottom: 2rem;
  }

  &__row-name {
    display: inline-block;
    font-weight: 300;
    margin-right: 0.3rem;
  }

  &__row-value {
    font-weight: 500;
    color: $black;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__price {
    font-weight: 800;
    font-size: 1.6rem;
    line-height: 2.1rem;

    color: $black;
  }

  &__btn {
    min-width: 15.3rem;
    padding: 0.9rem 2.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.2rem;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    position: relative;

    i {
      display: inline-block;
      background-color: rgba(255, 255, 255, 0.2);
      width: 1.6rem;
      height: 1.6rem;
      border-radius: 50%;
      color: #fff;
      font-weight: 500;
      font-style: normal;
      font-size: 1.1rem;
      line-height: 1.5rem;
      position: absolute;
      top: 0.3rem;
      right: 1.3rem;
    }
  }

  &__bottom-btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  &__edit-btn,
  &__delete-btn {
    width: 3rem;
    height: 3rem;
    background: grey;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.2rem;
    border-radius: 0.8rem;
    border: none;
    cursor: pointer;
    margin-left: 0.8rem;
  }
  &__btn-img {
    width: 2rem;
    height: 2rem;
  }
}
